.Record {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px 0;
  border-bottom: 1px solid #A6A2A2;
}

.Record h1 {
  font-size: 1.5em;
}

.Record > div {
  text-align: left;
}

.Record span {
  line-height: 4em;
  text-align: right;
}

.Record h1, .Record p {
  margin: 0;
}


